import calendar from '../../components/date-picker/date-picker.vue';

export default {
	data() {
		return {
			beginDate: '2019-05-06',
			endDate: '2019-05-07',
			showCaledar: false,
			dateStr: '',
			daysCount: 365,
			singleDate: true,
		};
	},
	components: {
		calendar
	},
	onLoad() {},
	onBackPress() {
		if (this.showCaledar !== false) {
			this.showCaledar = false;
			return true;
		}
	},
	methods: {
		change({
			choiceDate,
			dayCount
		}) {
			//参数解释
			//1.choiceDate 时间区间（开始时间和结束时间）
			//2.dayCount 共多少晚

			// console.log(choiceDate, dayCount);
			console.log('入住从 ' + choiceDate[0].re + '  到 ' + choiceDate[1].re + '  共 ' + dayCount + ' 晚');
		},
		change2({
			choiceDate,
			dayCount
		}) {
			this.dateStr =
				'入住从 ' + choiceDate[0].re + ' (星期' + choiceDate[0].week + ')  到 ' + choiceDate[1].re + '(星期' + choiceDate[1].week +
				')' + '  共 ' + dayCount + ' 晚 ';
		},
		change_single({
			choiceDate,
			dayCount
		}) {
			console.log(choiceDate, dayCount);
		}
	}
};
